import server from '../../server.js';
export default {
	data() {
		return {
			msgNumber:[]
		}
	},
	onShow:function(){
		this.getNoReadMsgNumber();
	},
	onLoad:function(){
		//this.getNoReadMsgNumber();
	},
	methods: {
		getPage:function(e){
			if(e==1){
				uni.navigateTo({
					url: 'zan'
				})
			}
			if(e==2){
				uni.navigateTo({
					url: 'mpl'
				})
			}
			if(e==3){
				uni.navigateTo({
					url: 'newFans'
				})
			}
		},
		getNoReadMsgNumber:function(){
			uni.showLoading({
				title: '加载中'
			});
			uni.request({
				method:'GET',
				url: "https://api.angeli.top/user.php?type=getMyNoRead", //仅为示例，并非真实接口地址。
				data: {
					
				},
				header: {
					'content-type': 'application/x-www-form-urlencoded',
					'Cookie':server.cookie
				},
				success: (res) => {
					console.log(res)
					if(res.data.code=="1"){
						this.msgNumber=res.data.data
					}else{
			
					}
					console.log(this.msgNumber)
				},
				complete() {
					uni.hideLoading();
				}
			});
		}
	}
}
